import { createContext, ReactNode, useContext, useMemo } from "react";
import { SnackbarKey, useSnackbar, VariantType } from "notistack";
import { IconButton } from "@mui/material";
import { Close } from "@mui/icons-material";

import { SnackActionType } from "./types";

const SnackAction = createContext<SnackActionType | undefined>(undefined);

export const SnackProvider = ({ children }: { children: ReactNode }) => {
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  const action = (snackbarId: SnackbarKey) => (
    <IconButton onClick={() => closeSnackbar(snackbarId)}>
      <Close sx={{ color: "#ffffff" }} />
    </IconButton>
  );

  const SnackActions = useMemo<SnackActionType>(
    () => ({
      setErrorMessage: (
        message: string,
        autoHideDuration: number | undefined = 5000,
        variant: VariantType = "error"
      ) => enqueueSnackbar(message, { action, variant, autoHideDuration }),
      setInfoMessage: (
        message: string,
        autoHideDuration: number | undefined = 5000,
        variant: VariantType = "info"
      ) => enqueueSnackbar(message, { action, variant, autoHideDuration }),
      setSuccessMessage: (
        message: string,
        autoHideDuration: number | undefined = 5000,
        variant: VariantType = "success"
      ) => enqueueSnackbar(message, { action, variant, autoHideDuration }),
      setWarningMessage: (
        message: string,
        autoHideDuration: number | undefined = 5000,
        variant: VariantType = "warning"
      ) => enqueueSnackbar(message, { action, variant, autoHideDuration }),
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <SnackAction.Provider value={SnackActions}>{children}</SnackAction.Provider>
  );
};

export const useSnacks = () => {
  const context = useContext(SnackAction);

  // If context is undefined, we know we used RadioGroupItem
  // outside of our provider so we can throw a more helpful
  // error!
  if (context === undefined) {
    throw Error(
      "Item must be used inside of SnackProvider, otherwise it will not function correctly."
    );
  }

  // Because of TypeScript's type narrowing, if we make it past
  // the error the compiler knows that context is always defined
  // at this point, so we don't need to do any conditional
  // checking on its values when we use this hook!
  return context;
};
